function sendMsg1 (event, arr) {
	// -enter 
	var msg = event.target.innerText;
	msg = msg.replace(/[\r\n]/g,"")		// 
	if(msg == "") {
		event.target.innerText = "";
		return;
	}
	event.target.innerText = "";
	var msgObj = {
		name: '',
		floor: arr.length+1,
		datetime: "6-21 13 : 00",
		img_src: '/static/img/touxiang2.d4dd0a8.jpg',
		dianzan: 0,
		dianzan_num: 1,		// 
		huifu_num: 0,		// 
		comment: msg
	}
	return msgObj;
}
function sendMsg2 (event, arr) {
	// - 
	var msg = $('#msg').html();
	if(msg == "" || msg == "<div><br></div><div><br></div>" || msg == "<div><br></div>") {
		$('#msg').html("");
		return;
	}
	var msgObj = {
		name: '',
		floor: arr.length+1,
		datetime: "6-21 13 : 00",
		img_src: '/static/img/touxiang2.d4dd0a8.jpg',
		dianzan: 0,
		dianzan_num: 1,		// 
		huifu_num: 0,		// 
		comment: msg
	}
//	this.answerList.push(msgObj);d
	$('#msg').html('');
	$('#msg').focus();
	return msgObj;
}

export {sendMsg1, sendMsg2};



